

import './index.scss';
import { Switch, Route, Redirect } from 'react-router-dom';
import LazyLoad from '&/lazyload';
const Foot = LazyLoad(() => import("~/components/Foot"));
export default class Index extends Component {
    render() {
        return (
            <div>
                <Switch>
                    <Route path="/index/home" component={LazyLoad(() => import('../children/home'))} />
                    <Route path="/index/classify" component={LazyLoad(() => import('../children/classify'))} />
                    <Route path="/index/mine" component={LazyLoad(() => import('../children/mine'))} />
                    <Route path="/index/cart" component={LazyLoad(() => import('../children/cart'))} />
                    <Route render={() => (<Redirect to='/index/home' />)} />
                </Switch>
                <Foot></Foot>
            </div>
        )
    }
}